/* eslint-disable no-sparse-arrays */
import React, { useEffect, useState } from "react";
import {
  Container,
  Grid,
  Paper,
  Typography,
  useMediaQuery,
  useTheme,
} from "@mui/material";
import Balance from "../../../components/Balance";
import CabinetTop from "../../../components/cabinetTop";
import { makeStyles } from "@mui/styles";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {
  getCategories,
  setLoading,
  setName,
} from "../../../redux/reducers/pay24";
import SkeletonPayments from "./Skeleton";
import { Box } from "@mui/material";
import binancePayLg2 from "../../../assets/images/binance-pay/binance-pay-2.webp";
import binancePayXs2 from "../../../assets/images/binance-pay/binance-pay-1.webp";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/swiper.min.css";

const PaymentForServices = ({ t, main, mode }) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const theme = useTheme();
  const sm = useMediaQuery(theme.breakpoints.down(768));

  const [filter, setFilter] = useState("");

  const { isLoading } = useSelector(({ Pay24Reducer }) => Pay24Reducer);

  const filteredCoin = React.useMemo(
    () =>
      Categories.filter((item) =>
        item.name
          .toLocaleLowerCase()
          .startsWith(filter.toLocaleLowerCase().trim())
      ),
    // eslint-disable-next-line
    [filter]
  );
  const CategoriesList = filter ? filteredCoin : Categories;

  useEffect(() => {
    dispatch(setLoading(true));
    dispatch(getCategories());
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    if (main) document.title = "Binance Pay";
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <>
      {main && <Box sx={{ height: 10, background: "#000" }} />}

      {main && (
        <Swiper
          className={classes.slider}
          loop={true}
          slidesPerView={1}
          autoplay={{
            delay: 5000,
            pauseOnMouseEnter: true,
            disableOnInteraction: false,
          }}
        >
          <SwiperSlide className={classes.slide}>
            <img
              src={sm ? binancePayXs2 : binancePayLg2}
              className={classes.img}
              alt="img"
            />
          </SwiperSlide>
        </Swiper>
      )}

      <Paper component="section" sx={{ pt: main && 4, pb: main && 4 }}>
        <Container maxWidth="lg">
          {!main && <Balance />}
          {/* {!main && <Templates t={t} />} */}

          <CabinetTop
            payment
            title={t("payments")}
            setFilter={setFilter}
            input
            placeHolder="Поиск услуг..."
          />

          <Grid container spacing={2}>
            {isLoading
              ? Array.from(Array(20).keys()).map((item, idx) => (
                  <Grid item key={idx} xs={12} sm={6} md={4} lg={3} xl={3}>
                    <SkeletonPayments />
                  </Grid>
                ))
              : CategoriesList.map((item, idx) => (
                  <Grid item key={idx} xs={12} sm={6} md={4} lg={3} xl={3}>
                    <Link
                      className={classes.box}
                      onClick={() => dispatch(setName(item.name))}
                      to={
                        !main
                          ? `/dashboard/payment-for-services/${item.api_id}`
                          : `/main/payment-for-services/binance-pay/${item.api_id}`
                      }
                    >
                      <Paper
                        sx={{
                          background: mode === "light" ? "#FFF" : "",
                          display: "flex",
                          boxShadow: mode === "light" && "0px 1px 0px #E2E8F0",
                          borderRadius: "8px",
                          height: "104px",
                          padding: "8px 8px 8px 16px",
                          justifyContent: "flex-start",
                          alignItems: "center",
                          overflow: "hidden",
                          transition: "all 0.2s linear",
                          "&:hover": {
                            boxShadow: "0px 0px 6px 2px #E2E8F0",
                          },
                        }}
                      >
                        <img src={item.logo_url} alt="" />
                        <Typography className={classes.title}>
                          {item.name}
                        </Typography>
                      </Paper>
                    </Link>
                  </Grid>
                ))}
          </Grid>
        </Container>
      </Paper>
    </>
  );
};

export default PaymentForServices;

const useStyles = makeStyles((theme) => ({
  slider: {
    width: "100%",
  },
  box: {
    "& img": {
      width: "40px",
      height: "40px",
      marginRight: 16,
      objectFit: "contain",
    },
  },

  title: {
    fontWeight: "500",
    fontSize: "18px",
    lineHeight: "23px",
    letterSpacing: "-0.02em",
    overflow: "hidden",
    textOverflow: "ellipsis",
  },

  img: {
    objectFit: "cover",
    width: "100%",
    height: "100%",
  },
}));

const Categories = [
  {
    id: 7,
    name: "Мобильная связь",
    api_id: 1,
    logo: null,
    logo_url:
      "https://terminal.pay24.asia/static/assets/images/category-logo-Mobile.png",
    order_id: 0,
  },
  {
    id: 9,
    name: "Пополнение банковских карт",
    api_id: 106,
    logo: null,
    logo_url:
      "https://terminal.pay24.asia/static/assets/images/category-logo-PopolnenieKart.png",
    order_id: 0,
  },
  {
    id: 10,
    name: "Интернет",
    api_id: 2,
    logo: null,
    logo_url:
      "https://terminal.pay24.asia/static/assets/images/category-logo-Internet.png",
    order_id: 0,
  },
  {
    id: 16,
    name: "Пополнение расчетных счетов",
    api_id: 105,
    logo: null,
    logo_url:
      "https://terminal.pay24.asia/static/assets/images/category-logo-PopolnenieSchetov.png",
    order_id: 1,
  },
  {
    id: 48,
    name: "ЭЛЕКТРОННЫЕ ДЕНЬГИ И КРИПТОВАЛЮТА",
    api_id: 90,
    logo: null,
    logo_url:
      "https://terminal.pay24.asia/static/assets/images/category-logo-ElektronnyeDengiIKriptovalyuta.png",
    order_id: 9,
  },
];
